import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'TVL Launchpad',
  description:
    'The most popular AMM on BSC by user count! Earn TVL through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by TVL Launchpad), NFTs, and more, on a platform you can trust.',
  image: ' ',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('TVL Launchpad')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('TVL Launchpad')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('TVL Launchpad')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('TVL Launchpad')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('TVL Launchpad')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('TVL Launchpad')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('TVL Launchpad')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('TVL Launchpad')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('TVL Launchpad')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('TVL Launchpad')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('TVL Launchpad')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('TVL Launchpad')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('TVL Launchpad')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('TVL Launchpad')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('TVL Launchpad')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('TVL Launchpad')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('TVL Launchpad')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('TVL Launchpad')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('TVL Launchpad')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('TVL Launchpad')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('TVL Launchpad')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('TVL Launchpad Info & Analytics')}`,
        description: 'View statistics for TVL Launchpad exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('TVL Launchpad Info & Analytics')}`,
        description: 'View statistics for TVL Launchpad exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Pools')} | ${t('TVL Launchpad Info & Analytics')}`,
        description: 'View statistics for TVL Launchpad exchanges.',
      }
    default:
      return null
  }
}
